import {RequestOptsConfig} from "./RequestOptsConfig";
import {EnvConfig} from "./EnvConfig";
import {EnvInstanceConfig} from "./EnvInstanceConfig";
import {JsonProperty} from "json-typescript-mapper";
import {MockServerConfig} from "./MockServerConfig";
export class TestSuiteConfig {
  id: string;
  type: string;
  skip: boolean;
  protocol: string;
  port: number;
  defaultRequestOpts: RequestOptsConfig;
  env: EnvConfig;
  @JsonProperty({clazz: EnvInstanceConfig, name: 'envInstances'})
  envInstances: EnvInstanceConfig[];
  mockServer: MockServerConfig;
}
